.am-icon {
  fill: currentColor;
  background-size: cover;
  width: 22 * 0.01rem;
  height: 22 * 0.01rem;
}

.am-icon-xxs {
  width: 15 * 0.01rem;
  height: 15 * 0.01rem;
}

.am-icon-xs {
  width: 18 * 0.01rem;
  height: 18 * 0.01rem;
}

.am-icon-sm {
  width: 21 * 0.01rem;
  height: 21 * 0.01rem;
}

.am-icon-md {
  width: 22 * 0.01rem;
  height: 22 * 0.01rem;
}

.am-icon-lg {
  width: 36 * 0.01rem;
  height: 36 * 0.01rem;
}

.am-icon-loading {
  -webkit-animation: cirle-anim 1s linear infinite;
  animation: cirle-anim 1s linear infinite;
}

@-webkit-keyframes cirle-anim {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes cirle-anim {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}